import { useEffect, useState } from 'react';
import { FaCheck, FaEdit } from 'react-icons/fa';
import { useHistory, useParams } from 'react-router';
import bancas from '../../data/bancas.json';
import Api from '../../shared/Api';
import { useDrawer } from '../../shared/components/Drawer';
import { EditQuestao } from '../../shared/components/EditQuestoes';
import { PageHeader } from '../../shared/components/PageHeader';
import { QuestaoItem } from '../../shared/components/QuestaoItem';
import { Relogio } from '../../shared/components/Relogio';
import { Aula } from '../../shared/interfaces';
type Params = {
  disciplina_id: string;
  questao_id: string;
  aula_id: string;
};

export function AulaDetail() {
  const [aula, setAula] = useState<Aula>();
  const params = useParams<Params>();
  const history = useHistory();
  const openDrawer = useDrawer()

  useEffect(() => {
    loadAula();
  }, []);

  async function loadAula() {
    try {
      const { data } = await Api.get<Aula>(`aulas/${params.aula_id}`);

      setAula(data);
    } catch (error) {}
  }

  return (
    <div>
      {!!aula && (
        <PageHeader
          backButton={!!aula}
          onBackPress={() => {
            params.questao_id
              ? history.push(
                  `/disciplinas/${params.disciplina_id}/aula/${params.aula_id}`
                )
              : history.push(`/disciplinas/${params.disciplina_id}`);
          }}
          title={`${String(aula.ordem).padStart(2, '0')} - ${aula?.name}`}
          subtitle={`${aula?.questoes.length || 0} questões`}>
          {!!aula && <Relogio aula={aula} />}
        </PageHeader>
      )}
      <div>
        {!params.questao_id && (
          <div className='bg-white rounded shadow-sm'>
            <div className='flex p-3 border-b gap-3'>
              <button
                onClick={() => openDrawer(EditQuestao, { aula_id: aula?.id}, result => {
                  if(result) {
                    loadAula()
                  }
                })}
                className='text-primary-600 border hover:shadow-sm transition-all text-sm flex-center gap-2 px-5 h-9 rounded-full ml-auto'>
                <FaEdit />
                <span>Editar Questões</span>
              </button>
              <button
                onClick={() =>
                  history.push(
                    `/disciplinas/${params.disciplina_id}/aula/${params.aula_id}/questao/${aula?.questoes[0].id}`
                  )
                }
                className='text-primary-600 border hover:shadow-sm transition-all text-sm flex-center gap-2 px-5 h-9 rounded-full'>
                <FaCheck />
                <span>Iniciar Questões</span>
              </button>
            </div>
            <div className='flex gap-0 p-5'>
              {aula &&
                Array.from(new Set(aula?.questoes.map((q) => q.banca)))
                  .map(
                    (banca) =>
                      bancas.find((b) => b.name === banca.toUpperCase()) || {
                        name: banca,
                        imageUrl: '',
                      }
                  )
                  .map((banca) => (
                    <div className='cursor-pointer hover:bg-gray-100 rounded p-3 py-5 relative transition-all mr-5'>
                      <div className='absolute w-5 h-5 flex-center rounded-full text-xs right-1 top-1 border-2 border-white bg-primary-600 text-white'>
                        {
                          aula?.questoes.filter(
                            (q) => q.banca.toUpperCase() === banca.name
                          ).length
                        }
                      </div>
                      <img
                        className='w-16 shadow-sm h-16 rounded-full'
                        src={banca.imageUrl}
                        alt=''
                      />
                      <h1 className='text-center text-gray-500 text-sm mt-3'>
                        {banca.name}
                      </h1>
                    </div>
                  ))}
            </div>
          </div>
        )}
        {!!params.questao_id && (
          <QuestaoItem questoes={aula?.questoes.map((item) => item.id) || []} />
        )}
      </div>
    </div>
  );
}
