import { toast } from 'react-toastify';
import { useDrawer } from '../../shared/components/Drawer';
import { useModal } from '../../shared/components/Modal';
import { PageHeader } from '../../shared/components/PageHeader';

export function Home() {
  const openModal = useModal();
  const openDrawer = useDrawer()

  function handlerClose(data: any) {
    console.log('handlerClose', data);
  }

  return (
    <div>
      <PageHeader title='Dashboard'>
        <button
          onClick={() =>
            openModal(() => <p className="p-12">Modal</p>, { name: 'wargas' }, handlerClose)
          }
          className='bg-primary-500 transition-all hover:bg-primary-600 text-white h-8 rounded-full px-5'>
          Open Modal
        </button>

        <button
          onClick={() =>
            openDrawer(() => <p>Drawer</p>, { name: 'wargas' }, handlerClose)
          }
          className='bg-primary-500 ml-3 transition-all hover:bg-primary-600 text-white h-8 rounded-full px-5'>
          Open Drawer
        </button>

        <button
          onClick={() =>
            toast.info('Isto é uma toast')
          }
          className='bg-primary-500 ml-3 transition-all hover:bg-primary-600 text-white h-8 rounded-full px-5'>
          Open Toast
        </button>
      </PageHeader>

      <div className='grid grid-cols-12 gap-4'>
        <div className='bg-white h-72 rounded p-5 shadow-sm col-span-3'>
          Chart 01
        </div>
        <div className='bg-white h-72 rounded p-5 shadow-sm col-span-9'>
          Chart 02
        </div>
        <div className='bg-white h-72 rounded p-5 shadow-sm col-span-9'>
          Chart 03
        </div>
        <div className='bg-white h-72 rounded p-5 shadow-sm col-span-3'>
          Chart 03
        </div>
      </div>
    </div>
  );
}
